import React, { useState } from "react";
import { useHistory } from "react-router-dom";
import { useDispatch } from "react-redux";
import { withRouter } from "react-router";
import Dropdown from "Component/Dropdown";
import Search from "Component/Search";
import { getStockTakeSummary, removeEntryModule } from "apiServices";
import SKUItem from "Component/SKUItem";
import { CSwitch } from "@coreui/react";
import loading from "assets/icons/loading/LOADING-MLS-GRAY.gif";
import "./index.scss";
import { selectSku } from './services'
import { filterByRemainingTarget, filterBySkuName, filterByUnfinishedTarget, filterCheckedSku, responseCheck, updateSummary } from "Page/StockEntry/GeneralServices/moduleServices";
import PopUp from "Component/Modal/PopUp";
import PopUpUnfinished from 'Component/Modal/PopUpUnfinished'
import Discard from 'Component/Modal/PopUpDiscard'
import SKUPagination from 'Component/SKUPagination'
import SingleView from 'Component/SingleViewModal'
import ItemListModal from 'Component/ItemListModal'
import { useDimension, useRepacking, useSingleView, useScanner } from "../GeneralServices";
const Repack = (props) => {
  const dispatch = useDispatch();
  const story = useHistory()
  const { height, emptyItem, isMobile, width } = useDimension(470)

  const {
    moduleName,
    thereAreRequest,
    moduleFirstLoad,
    warehouseUuid,
    status,
    isLoading,
    repackEntryData,
    summary,
    setSummary,
    localEntryData,
    setLocalEntryData,
    search,
    setSearch,
    area,
    areaId,
    setAreaId,
    areaDestinationId,
    discard,
    setDiscard,
    isCheckedTotal,
    targetRepack,
    setTargetRepack
  } = useRepacking({ dispatch })

  const {
    confirmation,
    setConfirmation,
    createPo,
    setCreatePo,
    setSingleViewData,
    setMoveAll
  } = useSingleView({ dispatch, setLocalEntryData, setSummary, setSearch, getStockTakeSummary, search, warehouseUuid, areaId, localEntryData })

  const { setShowScanner, showSkuList, setShowSkuList } = useScanner()

  //search
  const [category, setCategory] = useState()
  const [sortBy, setSortBy] = useState()
  const [unfinishedTarget, setUnfinishedTarget] = useState()
  const [selectAll, setSelectAll] = useState(false)

  //Condition
  let isReady = false
  let isCheckedTrue = summary?.map(data => {
    if (data?.checked) return true
  })
  if (thereAreRequest === 0 && (localEntryData && localEntryData?.jobPost?.list?.length > 0) && !selectAll) isReady = true
  if (thereAreRequest === 0 && selectAll && isCheckedTrue) isReady = true

  const isPlural = isCheckedTotal > 1 ? "s" : ""
  const noLength = summary?.length ? "flex-column" : "flex-row"

  return (
    <div className="w-100  stock-take-summary">
      <Discard
        discard={discard}
        module={'Inventory Entry - Repacking'}
        moduleEntry={repackEntryData}
        close={() => setDiscard(false)}
        discardEntry={(id) => removeEntryModule({ id, moduleName, dispatch, setDiscard })}
      />
      <PopUpUnfinished
        discard={discard}
        moduleFirstLoad={moduleFirstLoad}
        moduleEntry={repackEntryData}
        redirect={() => props.history.push("/inventory-entry/repacking/weight-entry")}
        previousPage={() => story.goBack()}
        setDiscard={() => setDiscard(true)}
      />

      <SingleView
        moduleName={moduleName}
        moduleEntry={localEntryData}
        areaMovement={area}
        createPo={createPo}
        setCreatePo={setCreatePo}
        setSingleViewData={setSingleViewData}
        setConfirmation={setConfirmation}
        confirmation={confirmation}
        setMoveAll={setMoveAll} />

      <PopUp status={status} />

      <ItemListModal
        moduleName={moduleName}
        areaId={areaId}
        areaName={area}
        warehouseUuid={warehouseUuid}
        showSkuList={showSkuList}
        setShowSkuList={setShowSkuList}
        dispatch={dispatch}
        setLocalEntryData={setLocalEntryData}
        localEntryData={localEntryData}
        setCreatePo={setCreatePo}
        thereAreRequest={thereAreRequest}
        setShowScanner={(value) => setShowScanner(value)} />

      <div className="px-3 pt-3 bg-white border-all radius-all d-md-flex dd-group">
        <div className="col-lg-3 px-0 stock-take-dropdown">
          <Dropdown
            isDisabled={true}
            show={true}
            title="Select Area"
            placeholder='Select Area'
            options={area}
            onChangeDropdown={(selected) => setAreaId(selected)}
            selectedValue={areaId}
            showTitle={true}
          />
        </div>
      </div>

      <div className="pt-3 pb-0 pl-3 pr-3 bg-white mt-3 border-all radius-all">
        <div className="d-md-flex d-lg-flex mb-3 justify-content-between">
          <div className="text LatoBold d-flex justify-content-between w-100 align-items-center sku-des">
            <div>
              Enter SKU or Description{" "}
              <span className="green">{`(${isCheckedTotal} Selected SKU${isPlural})`}</span>
            </div>
            <div className='d-flex' style={{ transition: 'ease-in 1s width', transitionDuration: '0.3s' }}>
              <div className='d-flex align-items-center mt-2 mt-md-0 justify-content-between mr-3'>
                <div className='emptyTextField LatoBold mr-3'>Target Repack</div>
                <CSwitch className="float-right" shape="pill" color="success" size={width <= 500 ? "sm" : "md"} checked={targetRepack} checked={targetRepack} onChange={() => isLoading ? false : setTargetRepack(!targetRepack)} />
              </div>
              <div className={`d-flex align-items-center mt-2 mt-md-0 justify-content-between ${targetRepack ? null : 'd-none'}`}>
                <div className='emptyTextField LatoBold mr-3'>Unfinished Target Repack</div>
                <CSwitch className="float-right" shape="pill" color="success" size={width <= 500 ? "sm" : "md"} checked={unfinishedTarget} checked={unfinishedTarget} onChange={() => setUnfinishedTarget(!unfinishedTarget)} />
              </div>
            </div>
          </div>
        </div>
        <Search
          moduleName={moduleName}
          setSortBy={setSortBy}
          category={category}
          setCategory={setCategory}
          areaId={areaId}
          showScanner={false}
          barcodeScanner={(value) => setShowScanner(value)}
          setSearch={(value) => setSearch(value)}
          placeholder={"Enter SKU"}
          filter={false}
          searchHandler={(desc) =>
            areaId ?
              getStockTakeSummary({ category, dispatch, desc, warehouseUuid, areaId, setModule: setSummary, module: localEntryData, targetRepack, moduleName }, setSearch(desc)) : setAreaId('empty')
          }
        />
        <div
          className={`mb-3 mt-3 d-flex align-items-center scroll-sku ${noLength} w-100 text-center`} style={{ minHeight: height, maxHeight: height }}>
          {summary?.length ? (
            summary?.filter(data => filterByUnfinishedTarget({ data, unfinishedTarget })).sort(function (a, b) { return filterBySkuName(a, b, sortBy?.value) })?.sort(function (a, b) {
              if (!sortBy?.target) return 0
              return filterByRemainingTarget(a, b, sortBy?.value, moduleName)
            })?.map((data, idx) => {
              return (
                <SKUItem
                  moduleName={moduleName}
                  targetRepackProgress={data?.targetRepackProgress}
                  target={targetRepack}
                  selectAll={selectAll}
                  key={idx}
                  img={data?.photo}
                  skuDescription={data?.skuName}
                  skuNumber={data?.skuNumber}
                  uomLabel="weight"
                  uomValue={data?.totalQty}
                  uom={data?.unitOfMeassures}
                  isCheckedHandler={(checked) =>
                    selectAll ?
                      updateSummary({ summary, setSummary, idx, checked }) :
                      selectSku({
                        idx,
                        checked,
                        dispatch,
                        setLocalEntryData,
                        moduleSummary: summary,
                        moduleEntry: localEntryData,
                        moduleName,
                        warehouseUuid,
                        areaId,
                        areaDestinationId
                      })
                  }
                  check={data?.checked}
                />
              );
            })
          ) : (
            <div className="w-100 text-center emptyTextField d-flex flex-column align-items-center justify-content-center" style={{ minHeight: emptyItem, maxHeight: emptyItem }}>
              {!responseCheck({ isLoading, summary }) ? (
                <img className="loading" src={loading} alt="loading" />
              ) : areaId && summary?.length === 0 ? (
                <div>
                  Sorry, we couldn't find any matches
                  for '<span className='LatoBold'>{search}</span>' in {areaId?.label} area.
                  <br />
                  Please Try search with another term.
                </div>
              ) : 'Enter SKU'}
            </div>
          )}
        </div>
      </div>
      <SKUPagination setSelectAll={() => setSelectAll(!selectAll)} selectAll={selectAll} />
      <div className={` ${isMobile ? 'd-block' : 'd-flex'} w-100 align-items-center justify-content-between mb-5`}>
        <div className={` ${isMobile ? 'mb-3' : false} w-100 d-flex`}>
          <button
            tabIndex='0'
            onClick={() => props.history.push("/inventory-entry/repacking/CSV-upload")}
            type="button"
            className={` ${isMobile ? 'w-50' : false} btn sku-item px-4 no-shadow border-all bg-white fontSizeButton`}
          >
            Upload File
          </button>
          <button
            tabIndex='0'
            onClick={() => props.history.push("/inventory-entry/repacking/upload-target-repack")}
            type="button"
            className={` ${isMobile ? 'w-50' : false} ml-2 text-nowrap btn sku-item px-2 no-shadow border-all bg-white fontSizeButton`}
          >
            Upload Target Repack
          </button>
        </div>
        <button
          tabIndex='0'
          onClick={() => {
            if (isReady) {
              filterCheckedSku({
                localEntryData,
                moduleName,
                summary,
                dispatch,
                warehouseUuid,
                areaId,
                areaIdDesposition: areaDestinationId,
                setLocalEntryData
              })
              props.history.push("/inventory-entry/repacking/weight-entry")
            }
          }
          }
          type="button"
          className={`${isMobile ? 'w-100' : false} btn sku-item px-5 fontSizeButton no-shadow text-white ${isReady ? "bg-green" : "bg-lineGrey"}`}>
          {(!localEntryData || localEntryData?.jobPost?.list?.length === 0) && thereAreRequest === 0 ? 'Next' : isReady ? 'Next' : 'Processing...'}
        </button>
      </div>
    </div>
  );
};

export default withRouter(Repack);
